html,
body {
  font-family: 'Raleway';
  user-select: none;
}
h1,
h2 {
  margin: 0;
}
a {
  text-decoration: none;
}
.wrap {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}
.line {
  width: 10vw;
  margin: 25px auto;
  background-color: gray;
}
@media (max-width: 1000px) {
  .line {
    width: 30vw !important;
  }
}
.wrapper {
  margin-left: auto;
  margin-right: auto;
}
.show-color {
  background-color: #25458e;
  transition: .5s;
}
header {
  height: 800px;
  background-image: url('../../image/cover.png');
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
}
header .navigation {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 1;
}
header .navigation .bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .navigation .bar .logo {
  display: flex;
  align-items: center;
  color: white;
  padding: .4em 0;
  margin-left: 45px;
  user-select: none;
}
header .navigation .bar .logo img {
  height: 100px;
  width: 100px;
}
header .navigation .bar .logo .name {
  width: 100%;
  margin-left: 2em;
  font-weight: bold;
}
header .navigation .bar .logo .name .first-line {
  font-size: 3.5vh;
}
header .navigation .bar .logo .name .second-line {
  font-size: 2.3vh;
}
header .navigation .bar .logo:hover {
  cursor: pointer;
}
header .navigation .bar .buttons {
  display: flex;
  align-items: center;
  color: white;
}
header .navigation .bar .buttons ul {
  display: flex;
  align-items: center;
}
header .navigation .bar .buttons ul li {
  margin: 0 10px;
}
header .navigation .bar .buttons ul li a {
  color: white;
  font-size: 1.28rem;
}
header .navigation .bar .buttons ul li a:hover {
  color: #fdd801;
  cursor: pointer;
}
header .navigation .bar .buttons .donate {
  margin: 0 30px 0 30px;
}
header .navigation .bar .buttons .donate a {
  background-color: #fdd801;
  padding: 10px 50px;
  font-size: 2.1vh;
  border-radius: 50px;
  font-weight: bold;
  color: black;
}
header .navigation .bar .buttons .donate a:hover {
  background-color: #e4c201;
}
header .hero .goal {
  display: flex;
  text-align: center;
  align-items: center;
  height: 800px;
}
header .hero .goal p {
  font-size: 3.4rem;
  color: #fff;
  margin: 0 auto;
}
main {
  padding: 4rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
main section .about-us {
  width: 100%;
  text-align: center;
}
main section .about-us span {
  font-size: 4vh;
  color: #25458e;
  font-weight: bold;
}
main section .about-us p {
  font-size: 3vh;
  margin: 2rem 3.6rem;
}
.our-cause {
  background-color: #25458e;
  display: flex;
  justify-content: space-between;
}
.our-cause .cause {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.our-cause .cause .video {
  display: flex;
  align-items: center;
  justify-content: center;
}
.our-cause .cause .video iframe {
  height: 33rem;
  width: 50vw;
}
.our-cause .cause .case-container {
  display: flex;
  align-items: center;
}
.our-cause .cause .case-container .case {
  margin-left: 2.5rem;
}
.our-cause .cause .case-container .case h2 {
  font-size: 2.38vw;
  color: white;
  font-weight: bold;
}
.our-cause .cause .case-container .case p {
  font-size: 1.47vw;
  width: 41vw;
  color: white;
}
.our-cause .cause .case-container .case .donate {
  padding-top: 1.56rem;
}
.our-cause .cause .case-container .case .donate a {
  padding: 10px 50px;
  font-size: 2.1vh;
  font-weight: bold;
  border: 2px solid #fdd801;
  border-radius: 50px;
  color: #fdd801;
}
.our-cause .cause .case-container .case .donate a:hover {
  transition: .2s;
  background-color: #fdd801;
  color: #25458e;
}
.quotes-container {
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quotes-container .quotation {
  display: flex;
  justify-content: center;
  align-items: center;
}
.quotes-container .quotation .qoute p {
  font-family: sans-serif;
  font-size: 2.3em;
}
.quotes-container .quotation .qoute span {
  padding-top: 60px;
  font-size: 1.5em;
  font-family: sans-serif;
}
.gallery {
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.gallery img {
  width: 100%;
  height: 100%;
  vertical-align: middle;
}
.gallery img:hover {
  transform: scale(1.2);
  transition: .3s;
  box-shadow: 1px 1px 41px 8px #000000;
}
.col-left,
.col-right {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}
.col-left .col-left-1 {
  grid-column-start: 1;
  grid-column-end: 4;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #25458e;
}
.col-left .col-left-1 span {
  font-size: 4vh;
  color: white;
  font-weight: bold;
}
.col-left .col-left-6 {
  grid-column-start: 2;
  grid-column-end: 4;
}
.col-right .col-right-4 {
  grid-column-start: 1;
  grid-column-end: 3;
}
.col-right .col-right-5 {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 4;
}
.contacts-us .contacts {
  text-align: center;
  margin-top: 4em;
}
.contacts-us .contacts h1 {
  font-size: 4.4vh;
  color: #25458e;
}
.contacts-us .contacts span {
  font-size: 3vh;
}
.contacts-us .contacts a {
  color: black;
  margin: 6px;
  font-size: 3vh;
}
.contacts-us .find-us {
  display: flex;
  justify-content: center;
  margin: 3em 0 2em 0;
  flex-wrap: wrap;
  align-content: space-between;
}
.contacts-us .find-us .location {
  margin-top: 10px;
}
.contacts-us .find-us .location iframe {
  height: 400px;
  width: 600px;
}
.contacts-us .find-us .location .location-info h2 {
  font-size: 4vh;
  color: #25458e;
}
.contacts-us .find-us .location .location-info p {
  font-size: 3vh;
}
.contacts-us .find-us .newsletter {
  margin-left: 40px;
}
.contacts-us .find-us .newsletter .newsletter-info h1 {
  font-size: 4vh;
  color: #25458e;
}
.contacts-us .find-us .newsletter .newsletter-info p {
  font-size: 3vh;
}
.contacts-us .find-us .newsletter form input {
  margin: 5px 0;
  width: 34rem;
  padding: 12px 25px;
  border: 1px solid gray;
  border-radius: 50px;
}
.contacts-us .find-us .newsletter form textarea {
  margin: 5px 0;
  padding: 1em;
  width: 34rem;
  height: 12rem;
  border: 1px solid gray;
  border-radius: 20px;
}
.contacts-us .find-us .newsletter form button {
  padding: 10px 50px;
  font-size: 2.1vh;
  border: 2px solid #25458e;
  border-radius: 50px;
  padding-top: 6px;
  margin-top: 5px;
  padding: 10px 60px;
  font-weight: bold;
  color: #25458e;
}
.contacts-us .find-us .newsletter form button:hover {
  background-color: #000040;
  color: #fff;
  transition: .2s;
}
footer {
  background-color: #333333;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}
footer small {
  color: white;
  font-size: 1.1em;
}
footer small a {
  color: #559c58;
}
@media (max-width: 1000px) {
  .buttons ul {
    display: none !important;
  }
  #video {
    width: 100vw;
  }
  #cause_container {
    justify-content: center;
    margin-bottom: 4em;
  }
  .case-container {
    margin-top: 5em;
  }
  .case-container .case {
    margin: 0 2em !important;
  }
  .case-container .case h2 {
    font-size: 4vh !important;
  }
  .case-container .case p {
    width: 100% !important;
    font-size: 3vh !important;
  }
  .location {
    margin-top: 2em;
    text-align: center;
  }
  #map {
    width: 100vw;
    height: 33rem;
  }
  .newsletter {
    padding: 6em 0 0 0;
    margin: 0 !important;
  }
  .newsletter-info {
    text-align: center;
  }
  form input,
  form textarea {
    width: 96vw !important;
    height: 2.5em;
    font-size: 1.88vh;
  }
  #logo-img {
    margin: .5em 0;
    height: 10vh !important;
    width: 10vh !important;
  }
  .gallery {
    grid-template-columns: 1fr !important;
  }
}
